@keyframes slide-in {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(0%);
  }
}
@keyframes pulse-outline {
  0% {
    outline-offset: 0;
  }
  25% {
    outline-offset: 4px;
  }
  50% {
    outline-offset: 0;
  }
  75% {
    outline-offset: 4px;
  }
  100% {
    outline-offset: 0;
  }
}
@keyframes loading-dots {
  0% {
    content: "";
  }
  25% {
    content: ".";
  }
  50% {
    content: "..";
  }
  100% {
    content: "...";
  }
}